$header_breakpoint_value: "1280px"; 

@mixin header_breakpoint($breakpoint: min ) {   

    @if $breakpoint == max {
        @media (max-width: ($header_breakpoint_value)) {
            @content;
        }
    } @else {
      @media (min-width: ($header_breakpoint_value)) {
        @content;
      } 
    }
}