@import '../base/vars';
@import '../base/media';
@import '../base/mixins';

.header {
  min-height: 50px; 
  background: #3F3F3F;
  display: flex;
  .header__logo {
    background: #E60000;
    display: grid;
    align-items: center;
    justify-content: center;
    padding: 0 30px;
    @include header_breakpoint(max) {
      padding: 5px 75px;
      width: 100%;
      min-height: 50px;
      img {
        max-width: 85%;
      }
    }
  }
  &__feedback, &__lang, &__search {
    display: grid;
    align-items: center;
    padding: 0 15px;
    position: relative;
    white-space: nowrap;
    a {
      font-size:1rem;
      text-decoration: underline;
      color: #d4d4d4;
      &:hover {
        text-decoration: none;
      }
    }
    @include header_breakpoint(max) {
      flex-grow: 1;
    }
  }
  &__lang {
    a {
      display: inline-block;
      padding: 7px;
    }
  }
  .language-dropdown {
    display: inline-block;
    position: relative;
    &:hover {
      .language-list {
        display: block;
      }
    }
  }
  .language-list {
    position: absolute;
    width: 100%;
    background: #fff;
    left: 0;
    border-radius: 3px;
    box-shadow: 0px 0px 0px 1px #c7c7c7 inset;
    color: #5d5d5d;
    display: none;
    li {
      cursor: pointer;
      padding: 0.5em 0.8em;
      font-size: 0.85rem;
      transition: 0.2s;
      &:hover {
        background: rgba(63, 63, 63, 0.5);
        color: #fff;
      }
    }
  }
  .language-selector-label {
    position: relative;
    &::after {
      content: '';
      border: solid #fff;
      border-width: 0 2px 2px 0;
      display: inline-block;
      padding: 3px;
      transform: rotate(45deg);
      vertical-align: middle;
      margin: -4px 0 0 10px;
    }
  }
  &__search {
    &_icon {
    }
    &_input {
      background: #fff;
      border-radius: 25px;
      border: none;
      padding: 10px 40px 10px 25px;
      height: 50px;
      box-shadow: 0 0 0px 3px #EAEAEA;
      width: 140px;
      @include placeholder {
        text-decoration: underline;
      }
      @include header_breakpoint(max) {
        padding: 10px 25px;
        width: 100%;
      }
    }
    &_submit {
      background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' fill='#{svg-bg($header-search-icon-input)}' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M17.838 16.394l-4.654-4.692a.52.52 0 01-.057-.654c1.908-2.93 1.463-6.957-1.352-9.363-2.71-2.314-6.833-2.235-9.457.176C-.695 4.63-.771 9.34 2.09 12.204c2.44 2.444 6.217 2.741 8.99.905a.517.517 0 01.657.057l4.644 4.682a.514.514 0 00.728.001l.727-.728c.2-.2.2-.525 0-.727zM3.544 10.75a5.112 5.112 0 010-7.214 5.093 5.093 0 017.203 0 5.112 5.112 0 010 7.213 5.093 5.093 0 01-7.203 0z'/%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-position: center;
      background-color: transparent;           
      fill: #000;
      border: none;
      font-size: 0;
      width: 50px;
      position: absolute;
      right: 10px;
      height: 50px;
      @include header_breakpoint(max) {
        display: none;
      }
    }
    @include header_breakpoint(max) {
      // display: none;
      position: absolute;
      left: 0;
      right: 0;
      top: 60px;
      transform: translate(0px, -200px);
      transition: 0.3s;
    }

  }
  @include header_breakpoint {
    min-height: 80px;
  }
}

.search-mob-label {
  position: absolute;
  right: 15px;
  top: 15px;
        @include header_breakpoint {
      display: none;
    } 
}
.search-mob:checked {
      @include header_breakpoint {
      display: none;
    } 
    & + .header__search {
      // display: block;
          transform: translate(0px, 0px);
    }
  
}

