/* all nav */
 
.stellarnav {
  position: relative;
  width: 100%;
  z-index: 9900;
  line-height: normal;
  .navMainWrap {
    background: #3F3F3F;
    display: flex;
    height: 100%;
    @include header_breakpoint {
      padding: 0 1em;
    }
    & > ul {
      & > li {
        margin-top: auto;
        margin-bottom: auto;
        include header_breakpoint {
          height: 100%;
        }
        @include header_breakpoint(max) {
          border-bottom: 1px solid #2E332F;
        }
        &.has-sub {
          a {
            @include header_breakpoint {
              padding-right: 10px;
            }
          }
        }
        &.drop-left {
          & > ul {
            right: 0;
          }
          & > ul ul {
            left: auto;
            right: 220px;
          }
        }
      }
    }
    li {
      & > a {
        &.dd-toggle {
          display: none;
          @include header_breakpoint(max) {
            position: absolute;
            top: 0;
            right: 0;
            padding: 0;
            width: 48px;
            height: 48px;
            text-align: center;
            z-index: 9999;
            border: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            outline: none;
            .icon-plus {
              border: solid #fff;
              border-width: 0 2px 2px 0;
              display: inline-block;
              padding: 3px;
              transform: rotate(-45deg);
            }
          }
        }
      }
      &.open {
        & > a.dd-toggle .icon-plus {
          transform: rotate(45deg);
        }
      }
    }
    ul ul {
      top: auto;
      width: 220px;
      position: absolute;
      z-index: 9900;
      text-align: left;
      display: none;
      background: #3f3f3f;
      @include header_breakpoint(max) {
        position: relative;
        width: auto;
        display: none;
        padding: 0 0 0.5em 1em;
      }
      & > li {
        a {
          padding: 0.5em 1.3em;
          @include header_breakpoint(max) {
            padding: 0.1em 1em 0.3em 0.9em;
          }
        }
      }
    }
    ul ul ul {
      @include header_breakpoint {
        top: 0;
        left: 220px;
      }   
    }
    @include header_breakpoint(max) {
      position: fixed;
      top: 50px;
      left: 0;
      display: block;
      bottom: 0;
      width: 100%;
      max-width: 280px;
      overflow-x: hidden;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
      transform: translate(-600px, 0px) translate3d(0,0,0);
      transition: 0.2s;
    }
  }
  .nav-main {
    display: flex;
    align-items: center;
    flex-grow: 1;
    li {
      a {
        padding: 15px;
        display: block;
        text-decoration: none;
        color: #fff;
        font-size: 1rem;
        transition: all .3s ease-out;
        @include header_breakpoint {
          height: 100%;
          padding: 1.6em 1.4em;
          line-height: 1.7;
        }
      }
    }
    & li {
      & > a {
        @include desktop_1500_max {
          padding-left: 0.7em;
          padding-right: 0.7em;
          white-space: nowrap;
        }
        @include desktop_1360_max {
          padding-left: 0.4em;  
          padding-right: 0.4em;  
        }
      }
    }
  }
  &.active {
    .navMainWrap {
        height: 100vh;
        display: flex;
        flex-direction: column;
       .nav-main {
        display: block;
        @include header_breakpoint(max) {
          flex-grow: 2;
        }
      }
    }
  }
  .menu-toggle {
    display: none;
    width: 22px;
    height: 22px;
    position: fixed;
    left: 16px;
    top: 11px;
    transform: translate3d(0, 0, 0);
    span, &::before, &::after {
      background-color: #fff;
      border-radius: 3px;
      content: '';
      display: block;
      height: 2px;
      margin: 5px 0;
      transition: all 0.2s ease-in-out;

    }
    @include header_breakpoint(max) {
      display: inline-block;
    }
  }
  &.active {
    .menu-toggle {
      span {
        transform: scale(0);
      }
      &::before {
        transform: translateY(5px) rotate(136deg);
      }
      &::after {
        transform: translateY(-9px) rotate(-135deg);
      }
    }
  }
  @include header_breakpoint(max) {
    width: auto;
  }
  li {
    position: relative;
    list-style: none;
    display: block;
    @include header_breakpoint {
      &.has-sub {
        & > a {
          &::after {
            content: '';
            border: solid #fff;
            border-width: 0 2px 2px 0;
            display: inline-block;
            padding: 3px;
            transform: rotate(45deg);
            vertical-align: middle;
            margin: -4px 0 0 8px;
          }
        }
      }
      li {
        &.has-sub {
         & > a {
            &::after {
              transform: rotate(-45deg) translateY(-50%);
              top: 50%;
              position: absolute;
              right: 10px;
            }
          }
        } 
      }
    }
    &.drop-left li.has-sub>a:after {
      transform: rotate(-45deg) translateY(-50%);
      top: 50%;
      position: absolute;
      right: 10px;
    }
  }
  &.active {
    .navMainWrap {
      @include header_breakpoint(max) {
        transform: translate(0, 0);
      }
    }
  }
  &.fixed {
    position: fixed;
    transform: translate3d(0,0,0);
    width: 100%;
    top: 0;
    left: 0;
    z-index: 9999;
  }
}
 
